interface SelectedItem {
    itemId: string,
    count: number
}

export function MainApp() {
    
    const [products, setProducts] = useState<Product[]>([])
    
    const {tg} = useTelegram()

    useEffect(() => {
        tg.ready();
        
        const urlParams = new URLSearchParams(window.location.search);

        setProducts(JSON.parse(urlParams.get('products') ?? "[]") as Product[])
    }, []);
    
    const [selectedItems, setSelectedItems] = useState<SelectedItem[]>([])

    const onSendData = useCallback(() => {
        const data = {
            selectedItems
        }
        tg.sendData(JSON.stringify(data));
        setSelectedItems([])
    }, [selectedItems])
    
    useEffect(() => {
        tg.onEvent('mainButtonClicked', onSendData)
        return () => {
            tg.offEvent('mainButtonClicked', onSendData)
        }
    }, [onSendData])

    useEffect(() => {
        tg.MainButton.setParams({
            text: ' '
        })
    }, [])

    useEffect(() => {
        if(!selectedItems.length) {
            tg.MainButton.hide();
        } else {
            tg.MainButton.show();
        }
    }, [selectedItems])
    
    // const onApplyOrder = () => {
    //     const totalPrice = selectedItems.reduce((prev, item) => prev + item.count * (products.find(x => x.id == item.itemId)?.price)!, 0)
    //     Modal.confirm({
    //         title: ' ,    ?',
    //         content: `    ${totalPrice} .`,
    //         onOk: () => {
    //             tg?.sendData(JSON.stringify(selectedItems));
    //             setSelectedItems([])
    //             // Telegram send data
    //         }
    //     })
    // }
    
    return <div><Card title="iPhone's Market">
        {
            products.map(x => (
                <Card.Grid key={x.id} style={styles.gridStyle} hoverable={false}>
                    <Row gutter={[10,10]}>
                        <Col span={24}>
                            <Image src={x.imageUrl}/>
                        </Col>
                        <Col span={24}>{x.name}</Col> 
                        <Col span={24}>{x.price} .</Col> 
                        <Col span={24}>
                            <Button type={"primary"} onClick={() => {
                                const item = selectedItems.find(i => i.itemId == x.id)
                                if (!item) {
                                    setSelectedItems([
                                        ...selectedItems, {
                                        itemId: x.id,
                                            count: 1
                                        }
                                    ])
                                } else {
                                    setSelectedItems([
                                        ...selectedItems.filter(i => i.itemId != x.id),
                                        {
                                            itemId: x.id,
                                            count: item.count + 1
                                        }
                                    ])
                                }
                            }}></Button>
                        </Col> 
                        <Col span={24}>
                            {
                                selectedItems.some(i => i.itemId == x.id) ? ` ${
                                    selectedItems.find(i => i.itemId == x.id)?.count 
                                } .`  : ''
                            }
                        </Col>
                    </Row>
                </Card.Grid>
            ))
        }
    </Card>
        {/*<div style={styles.footer}>*/}
        {/*    <Button type={"primary"} size={'large'} style={styles.button} onClick={onApplyOrder}>*/}
        {/*         */}
        {/*    </Button>*/}
        {/*</div>*/}
    </div>
}